<template>
    <b-row>
        <b-col cols="12">
            <h3>Carriers page</h3>
            <b-table :items="carriers" striped></b-table>
            <router-view></router-view>
        </b-col>
    </b-row>
</template>

<script>
import Backend from '../../services/backend';

const backend = new Backend();

export default {
    name: 'CarriersPage',
    components: {

    },
    data() {
        return {
            carriers: []
        };
    },
    created() {
        backend.getRequest('services/carriers/')
            .then(response => {
                this.carriers = response.data;
            })
            .catch(error => {
                console.log(error.response);
            });
    }
};
</script>

