import axios from 'axios';

let instance = null;

class Backend {
    API_URL = 'https://afterparty.app/api/';
    authAxios = null;
    token = null;
    constructor() {
        if (!instance) {
            instance = this;
            let storageToken = localStorage.getItem('token');
            if (storageToken) {
                this.token = storageToken;
                this.setAuthAxios(storageToken);
            }
        }
        return instance;
    }

    auth(data) {
        return new Promise((resolve, reject) => {
            axios.post(this.API_URL + 'auth/', data)
                .then((response) => {
                    this.token = response.data.token;
                    this.setAuthAxios(response.data.token);
                    localStorage.setItem('token', response.data.token);
                    resolve(response);
                })
                .catch(error => {
                    console.log(error);
                    reject(error);
                });
        });
    }

    getRequest(path) {
        return this.authAxios.get(path);
    }

    postRequest(path, data) {
        return this.authAxios.post(path, data);
    }

    setAuthAxios(token) {
        this.authAxios = axios.create({
            baseURL: this.API_URL,
            headers: {
                Authorization: 'Bearer ' + token,
                'Content-Type': 'application/json'
            }
        });
    }
}

export default Backend;
