<template>
    <div>
        <h3>Address Add Page</h3>
        <b-form class="main-container">
            <b-form-group
                label="Address"
                label-for="inputAddress"
            >
                <b-card
                    v-if="errorMessage"
                    border-variant="danger"
                    text-variant="danger"
                    class="mb-3"
                >
                    {{ errorMessage }}
                </b-card>
                <b-card
                    v-if="successMessage"
                    border-variant="success"
                    text-variant="success"
                    class="mb-3"
                >
                    {{ successMessage }}
                </b-card>
                <b-form-input
                    id="inputAddress"
                    v-model="$v.address.$model"
                    v-bind:class="{ 'is-invalid': $v.address.$error }">
                    ></b-form-input>
            </b-form-group>
            <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Add</b-button>
        </b-form>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';

const backend = new Backend();

export default {
    name: 'AddressAddPage',
    components: {

    },
    data() {
        return {
            address: '',
            hasBackendError: false,
            successMessage: null,
            errorMessage: null
        };
    },
    validations: {
        address: {
            required
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.address.$touch();
                return;
            }
            let data = {
                name: this.address
            };
            backend.postRequest('services/addresses/', data)
                .then((response) => {
                    let addressName = response.data.name;
                    this.hasBackendError = false;
                    this.errorMessage = null;
                    this.successMessage = `${addressName} was added successfully`;
                    this.address = '';
                    setTimeout(() => this.$v.address.$reset(), 0);
                })
                .catch((error) => {
                    this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                        'Something went wrong. Please try again later';
                });
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
