<template>
    <div v-bind:class="{ 'left-menu-hidden':  leftMenuHidden}">
        <admin-header></admin-header>
        <aside class="left-menu">
            <left-menu></left-menu>
        </aside>
        <div class="main-content">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
import LeftMenu from './LeftMenu';
import AdminHeader from './AdminHeader';
import Backend from '../services/backend';

const backend = new Backend();

export default {
    name: 'AdminPage',
    components: {
        LeftMenu,
        AdminHeader
    },
    data() {
        return {
        };
    },
    computed: {
        leftMenuHidden() {
            return this.$store.state.leftMenuHidden;
        }
    },
    created() {
        window.addEventListener('resize', (e) => {
            if (window.innerWidth < 768 && !this.$store.state.leftMenuHidden) {
                this.$store.commit('toggleLeftMenu');
            } else if (window.innerWidth >= 768 && this.$store.state.leftMenuHidden) {
                this.$store.commit('toggleLeftMenu');
            }
        });
    },
    methods: {
        logout() {
            localStorage.removeItem('token');
            this.$router.push({ name: 'login' });
        }
    }
};

</script>

<style scoped>
    .left-menu {
        position: fixed;
        width: 230px;
        height: calc(100% - 56px);
        background: #f2f3f4;
        transition: transform 0.3s ease;
        z-index: 1;
        border-right: 1px solid #ced4da;
    }
    .left-menu-hidden .left-menu {
        transform: translateX(-230px);
    }
    .left-menu-hidden .main-content{
        margin-left: 0;
    }
    .main-content {
        margin-left: 230px;
        padding: 30px 20px;
        transition: all 0.3s ease;
    }

    @media screen and (max-width: 768px) {
        .main-content {
            margin-left: 0;
        }
    }
</style>
