<template>
    <b-row>
        <b-col cols="12">
            <h3>Addresses page</h3>
            <b-table :items="addresses" striped></b-table>
            <router-view></router-view>
        </b-col>
    </b-row>
</template>

<script>
import Backend from '../../services/backend';

const backend = new Backend();

export default {
    name: 'AddressesPage',
    components: {

    },
    data() {
        return {
            addresses: []
        };
    },
    created() {
        backend.getRequest('services/addresses')
            .then(response => {
                console.log(response);
                this.addresses = response.data;
            })
            .catch(error => {
                console.log(error.response);
            });
    }
};
</script>

