import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    strict: true,
    state: {
        address: 'HOME',
        leftMenuHidden: window.innerWidth < 768
    },

    mutations: {
        toggleLeftMenu(state) {
            state.leftMenuHidden = !state.leftMenuHidden;
        }
    },

    actions: {

    }
});
