<template>
    <b-container class="mt-5">
        <b-row align-h="center">
            <b-col cols="12" md="8">
                 <b-card
                    header="Netportal administration"
                    header-bg-variant="primary"
                    header-text-variant="white"
                 >
                    <b-form class="main-container" @submit="onSubmit">
                        <b-card
                            v-if="hasBackendError"
                            border-variant="danger"
                            text-variant="danger"
                            class="mb-3"
                        >
                            Please enter the correct username and password for a staff account. Note that both fields may be case-sensitive.
                        </b-card>
                        <b-form-group
                            label="Username"
                            label-for="inputUsername"
                        >
                            <b-form-input
                                id="inputUsername"
                                v-model="$v.username.$model"
                                v-bind:class="{ 'is-invalid': $v.username.$error }">
                            ></b-form-input>
                        </b-form-group>
                        <b-form-group
                            label="Password"
                            label-for="inputPassword"
                        >
                            <b-form-input
                                type="password"
                                id="inputPassword"
                                v-model="$v.password.$model"
                                v-bind:class="{ 'is-invalid': $v.password.$error }">
                            ></b-form-input>
                        </b-form-group>
                        <div>
                            <b-button 
                                type="submit" 
                                variant="primary" 
                                class="px-4"
                                :disabled="isLoading"
                            >
                                Log in
                            </b-button>
                            <preloader class="ml-2 mt-1" v-if="isLoading" size="30px"/>
                        </div>
                    </b-form>
                </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import Backend from '../services/backend';
import { required } from 'vuelidate/lib/validators';
import Preloader from './Preloader';

let backend = new Backend();

export default {
    name: 'Login',
    components: {
        Preloader
    },
    data() {
        return {
            username: '',
            password: '',
            hasBackendError: false,
            isLoading: false
        };
    },
    validations: {
        username: {
            required
        },
        password: {
            required
        }
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.username.$touch();
                this.$v.password.$touch();
                return;
            }
            let data = {
                username: this.username, 
                password: this.password
            };
            this.isLoading = true;
            backend.auth(data)
                .then(response => {
                    console.log(response);
                    this.hasBackendError = false;
                    this.$router.push({ name: 'addresses' });
                    this.isLoading = false;
                })
                .catch(error => {
                    console.log(error.response);
                    if (error.response) {
                        this.hasBackendError = true;
                        this.password = '';
                        this.isLoading = false;
                    }
                });
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
